// =
// Animations
// ==================================================

@charset "UTF-8";

.animated {
	.animation-duration(.5s);
	.animation-fill-mode(both);

    &.infinite {
    	.animation-iteration-count(infinite);
    }

    &.hinge,
    &.flash {
    	.animation-duration(2s);
    }

    &.flipOutX,
    &.flipOutY,
    &.bounceIn,
    &.bounceOut {
    	.animation-duration(.75s);
    }

    &.flip {
    	-webkit-backface-visibility: visible;
    	backface-visibility: visible;
    	.animation-name(flip);
    }
}

